import React, {Component} from 'react';
import ProjectTab from "../views/tabs/projectTab";
import projectsData from "../../domain/config/data/projects.json"
import pageHead from "../../../../../packages/ext/pageTitle/singletons/pageTitle";

export default class ProjectsComponent extends Component {

    private title = 'Проекты';

    render() {
        pageHead.setPageTitle(this.title);
        return (
            <>
                <h3 className="fw-bold">
                    {this.title}
                </h3>
                <ProjectTab projects={projectsData}/>
            </>
        );
    }
}
