import React from 'react';
import aboutData from "../../../domain/config/data/about.json";
import contactsData from "../../../domain/config/data/contacts.json"
import MainContactTab from "./mainContactTab";
import Html from "../../../../../../packages/widgets/html/Html";
import StringHelper from "../../../../../../packages/ext/string/helpers/StringHelper";

function getAge(dateString) {
    var today = new Date();
    var birthDate = new Date(dateString);
    var age = today.getFullYear() - birthDate.getFullYear();
    var m = today.getMonth() - birthDate.getMonth();
    if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
        age--;
    }
    return age;
}

export default function AboutTab(props) {
    let genderTitle = aboutData.gender === "male" ? 'Мужчина' : 'Женщина';
    let birthday = new Date(aboutData.birthday);
    let age = getAge(aboutData.birthday);
    return (
        <>

            <div className="card mt-3">
                <div className="card-body">

                    <h5 className="card-title">
                        {aboutData.fullName}
                    </h5>

                    <p>
                        {genderTitle}, {age} {StringHelper.declOfNum(age, ['год', 'года', 'лет'])} ({birthday.toLocaleDateString()})
                    </p>

                    <h5 className="card-title">
                        Контакты
                    </h5>

                    <MainContactTab contacts={contactsData}/>

                    <p>
                        <Html>
                            {aboutData.aboutMe}
                        </Html>

                    </p>
                </div>
            </div>



        </>
    );
};
