import React from 'react';
import ImageSliderWidget from "../../../../../../packages/widgets/imageSlider/ImageSliderWidget";
import Html from "../../../../../../packages/widgets/html/Html";

export default function ProjectTab(props) {
    let items = props.projects;
    return (
        <>
            {items.map(function (item, key) {

                return (
                    <div key={key} className="card mt-3">
                        <div className="card-body">

                            {item.images ? (
                                <div className="float-left" style={{width: '400px', maxWidth: '100%', margin: "0px 40px 10px 0px"}}>
                                    <ImageSliderWidget items={item.images}/>
                                </div>
                            ) : null}

                            <small className="float-right">
                                <span title="Компания">{item.company}</span> | <span title="Должность">{item.position}</span>
                            </small>

                            <h5 className="card-title">
                                {item.title}
                            </h5>

                            {item.site ? (
                                <p className="card-text">
                                    <i className="fas fa-link"></i>
                                    &nbsp;
                                    <a href={item.site} target="_blank" rel="noreferrer">
                                        {item.site}
                                    </a>
                                </p>
                            ) : null}


                            <p className="card-text">
                                <Html>
                                    {item.description}
                                </Html>
                            </p>

                            {/*{item.responsibilities && item.responsibilities.length ? (
                                <>
                                    <h6 className="card-subtitle mb-2">
                                        Обязанности
                                    </h6>
                                    <ul>
                                        {item.responsibilities.map(function (item, key) {
                                            return (
                                                <li key={key}>
                                                    {item}
                                                </li>
                                            );
                                        })}
                                    </ul>
                                </>
                            ) : null}*/}

                            <h6 className="card-subtitle mb-2">
                                Основной стек
                            </h6>

                            <p className="card-text">
                                {item.techUnit.map(function (item, key) {
                                    return (
                                        <span key={key} className="badge badge-primary ml-1 mr-1">
                                            {item}
                                        </span>
                                    );
                                })}
                            </p>

                        </div>
                    </div>
                );
            })}

        </>
    );
};
