import React from 'react';
import SkillBadge from "../skillBadge";
import SkillProgressEnum from "../../../domain/enums/SkillProgressEnum";
import getColorByLevel from "../../helpers/skillBadge";

export default function SkillsTab(props) {
    return (
        <>
            <div className="card mt-3">
                <div className="card-body">
                    {props['skills'].map(function (item, key) {
                        return <SkillBadge key={key} title={item.title} level={item.level}/>;
                    })}
                </div>
            </div>

            <div className="card mt-3">
                <div className="card-body">
                    <h5 className="card-title">
                        Условное обозначение уровня знаний
                    </h5>
                    <ul>
                        <li>
                            <SkillBadge title="Отлично" level={SkillProgressEnum.VERY_HIGH}/> - <span
                            style={{color: getColorByLevel(SkillProgressEnum.VERY_HIGH).foregroundColor}}>знаю отлично</span>
                        </li>
                        <li>
                            <SkillBadge title="Хорошо" level={SkillProgressEnum.HIGH}/> - <span
                            style={{color: getColorByLevel(SkillProgressEnum.HIGH).foregroundColor}}>знаю хорошо</span>
                        </li>
                        <li>
                            <SkillBadge title="Нормально" level={SkillProgressEnum.NORMAL}/> - <span
                            style={{color: getColorByLevel(SkillProgressEnum.NORMAL).foregroundColor}}>знаю достаточно</span>
                        </li>
                        <li>
                            <SkillBadge title="Поверхностно" level={SkillProgressEnum.LOW}/> - <span
                            style={{color: getColorByLevel(SkillProgressEnum.LOW).foregroundColor}}>знаю поверхностно</span>
                        </li>
                        <li>
                            <SkillBadge title="Плохо" level={SkillProgressEnum.VERY_LOW}/> - <span
                            style={{color: getColorByLevel(SkillProgressEnum.VERY_LOW).foregroundColor}}>знаю плохо</span>
                        </li>
                    </ul>
                </div>
            </div>
        </>
    );
};
