import React, {Component} from 'react';
import linksData from "../../domain/config/data/links.json"
import LinksTab from "../views/tabs/linksTab";
import pageHead from "../../../../../packages/ext/pageTitle/singletons/pageTitle";

export default class LinksComponent extends Component {

    private title = 'Ссылки';

    render() {
        pageHead.setPageTitle(this.title);
        return (
            <>
                <h3 className="fw-bold">
                    {this.title}
                </h3>
                <LinksTab links={linksData}/>
            </>
        );
    }
}
